// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router/index'
import Vuex from 'vuex'

//添加Fastclick移除移动端点击延迟
FastClick.attach(document.body)
Vue.use(Vuex)

Vue.config.productionTip = false

// vuex 状态管理
const store = new Vuex.Store({
	state : {								// state仓库，储存数据
		carousel: [
           	{
              url : 'javascript:;',
              img : require('./assets/banner1.jpg')
           	},
           	{
              url : 'javascript:;',
              img : require('./assets/banner2.jpg')
           	},
           	{
              url : 'javascript:;',
              img : require('./assets/banner3.jpg')
           	},
        ],
	},
	mutations : {							// 更新state状态，可通过actions更新，mutations 不可异步操作
		// add : (state,num) => {
		// 	store.number += num;
		// },
	},
	actions : {								// 执行mutations更新，与其区别在于，actions 可执行任意异步操作（ajax）
		// addNum : (context,num) => {
		// 	context.commit('add',num);
		// },
	},
	getters : {								// 获取state数据
		getCar : state => {
			return state.carousel
		}
	},
 	//strict: true 							// 在严格模式下，任何 mutation 处理函数以外修改 Vuex state 都会抛出错误。
});



/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app-box')



