import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      name: 'Error',
      component:resolve => require(['@/pages/Error'],resolve),  
    },
    {
      path: '/',
      name: '首页',
      component:resolve => require(['@/pages/index'],resolve),  
    },
    {
      path: '/about',
      name: '关于我们',
      component:resolve => require(['@/pages/about'],resolve),	
    },
    {
      path: '/service',
      name: '服务项目',
      component:resolve => require(['@/pages/service'],resolve),	
    },
    {
      path: '/news',
      name: '新闻资讯',
      component:resolve => require(['@/pages/news'],resolve),	
    },
    {
      path: '/cases',
      name: '精彩案例',
      component:resolve => require(['@/pages/cases'],resolve),	
    },
    {
      path: '/team',
      name: '设计团队',
      component:resolve => require(['@/pages/team'],resolve),	
    },
    {
      path: '/contact',
      name: '联系我们',
      component:resolve => require(['@/pages/contact'],resolve),  
    },
    {
      path: '/news/msg_news',
      name: '',
      component:resolve => require(['@/pages/msg_news'],resolve),  
    },
  ]
});


